#footer {
    margin-top: 40px;
}
#footer h2 {
    margin-bottom: 1em;
    font-size: 1.75em;
}
#footer input[type="submit"],
#footer input[type="reset"],
#footer input[type="button"],
#footer button,
#footer .button {
    background-color: #5a5a5a;
    color: #ffffff !important;
    -webkit-transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
    border-radius: 0;
    border: 0;
    cursor: pointer;
    display: inline-block;
    font-size: 0.8em;
    font-weight: 700;
    height: 3.5em;
    line-height: 3.65em;
    padding: 0 2.25em;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
}
#footer a,
#footer form,
#footer header,
#footer label,
#footer li,
#footer ol,
#footer p,
#footer span,
#footer ul {
    border: 0;
    padding: 0;
    font-size: 100%;
}
#footer input[type="submit"].alt,
#footer #footer input[type="reset"].alt,
#footer #footer input[type="button"].alt,
#footer #footer button.alt,
#footer #footer .button.alt {
    background-color: transparent;
    -webkit-box-shadow: inset 0 0 0 2px #fff;
    box-shadow: inset 0 0 0 2px #fff;
    color: #ffffff !important;
}
#footer form {
    margin: 0 0 2em;
}
#footer form .field {
    margin: 0 0 2em;
}
#footer form .field.half {
    width: 50%;
    float: left;
    padding: 0 0 0 1em;
}
#footer form .field.half.first {
    padding: 0 1em 0 0;
}
#footer form > .actions {
    margin: 2.5em 0 0 0 !important;
}
@media screen and (max-width: 736px) {
    #footer form .field {
        margin: 0 0 1.5em;
    }
    #footer form .field.half {
        padding: 0 0 0 0.75em;
    }
    #footer form .field.half.first {
        padding: 0 0.75em 0 0;
    }
    #footer form > .actions {
        margin: 2em 0 0 0 !important;
    }
}
@media screen and (max-width: 480px) {
    #footer form .field.half {
        width: 100%;
        float: none;
        padding: 0;
    }
    #footer form .field.half.first {
        padding: 0;
    }
}
#footer label {
    display: block;
    font-size: 0.9em;
    text-transform: uppercase;
    font-weight: 700;
    margin: 0 0 1em;
}
#footer input[type="text"],
#footer input[type="password"],
#footer input[type="email"],
#footer select,
#footer textarea {
    -moz-appearance: none;
    -webkit-appearance: none;
    -ms-appearance: none;
    appearance: none;
    border-radius: 0;
    border: none;
    border: solid 3px;
    color: inherit;
    display: block;
    outline: 0;
    padding: 0 1em;
    text-decoration: none;
    width: 100%;
}
#footer .select-wrapper {
    text-decoration: none;
    display: block;
    position: relative;
}
#footer input[type="text"],
#footer input[type="password"],
#footer input[type="email"],
#footer select {
    height: 2.75em;
}
#footer textarea {
    padding: 0.75em 1em;
}
#footer input[type="checkbox"],
#footer input[type="radio"] {
    -moz-appearance: none;
    -webkit-appearance: none;
    -ms-appearance: none;
    appearance: none;
    display: block;
    float: left;
    margin-right: -2em;
    opacity: 0;
    width: 1em;
    z-index: -1;
}
#footer input[type="checkbox"]+label,
#footer input[type="radio"]+label {
    text-decoration: none;
    cursor: pointer;
    display: inline-block;
    font-size: 1em;
    font-weight: 300;
    padding-left: 2.4em;
    padding-right: 0.75em;
    position: relative;
}
#footer label {
    color: #e5474b;
}
#footer input[type="text"],
#footer input[type="password"],
#footer input[type="email"],
#footer select,
#footer textarea {
    background: none;
    border-color: #fceced;
}
#footer li {
    margin: 0;
    border: 0;
    font: inherit;
}
#footer ol {
    list-style: decimal;
    margin: 0 0 2em;
    padding-left: 1.25em;
}
#footer ol li {
    padding-left: 0.25em;
}
#footer ul {
    list-style: disc;
    margin: 0 0 2em;
    padding-left: 1em;
}
#footer ul li {
    padding-left: 0.5em;
}
#footer ul.alt {
    list-style: none;
    padding-left: 0;
}
#footer ul.alt li {
    border-top: solid 1px;
    padding: 0.5em 0;
}
#footer ul.alt li:first-child {
    border-top: 0;
    padding-top: 0;
}
#footer ul.actions {
    cursor: default;
    list-style: none;
    padding-left: 0;
}
#footer ul.actions li {
    display: inline-block;
    padding: 0 1em 0 0;
    vertical-align: middle;
}
#footer ul.actions li:last-child {
    padding-right: 0;
}
#footer ul.alt li {
    border-top-color: #e5474b;
}
section .inner {
    max-width: 65em;
    width: calc(100% - 6em);
    margin: 0 auto;
    width: 100%;
}
#header {
    background: #1c1c1c;
    color: #d5d5d5;
    cursor: default;
    height: 5em;
    left: 0;
    line-height: 5em;
    position: fixed;
    text-align: right;
    top: 0;
    width: 100%;
    z-index: 10001;
}
@media screen and (max-width: 480px) {
    #header .inner {
        max-width: 90%;
        width: 90%;
    }
}
@media screen and (max-width: 736px) {
    #header a {
        padding: 0 0.5em;
    }
}
@media screen and (max-width: 480px) {
    #header {
        min-width: 320px;
    }
}
